import React, {Component} from 'react'
import axios from 'axios'

export default class Contact extends Component{
    constructor(props){
        super(props);
        this.state = {
            name: '',
            email: '',
            message: '',
            subject: ''
        }
    }
    formSubmit = (e) => {
        e.preventDefault();
        let data = {name: this.state.name, email: this.state.email, subject: this.state.subject, message: this.state.subject};
        axios.post('API_URI', data)
            .then(res => {
                this.setState({sent: true},this.resetForm());
            })
            .catch(() => {
                console.log('Message not sent');
            })
    }
    resetForm = () => {
        this.state = {
            name: '',
            email: '',
            message: '',
            subject: ''
        }
    }
    render(){
        return(
            <section id="contact">
                <div className="row section-head">
                    <div className="col full">
                        <h2>Have a question or suggestion?</h2>
                        <p className="desc">Get in touch with me.</p>
                    </div>
                </div>
                <div className="row">
                    <div className="col g-7">
                        <form name="contactForm" id="contactForm" method="post" action="" onSubmit={(e) => this.formSubmit(e)}>
                            <fieldset>
                                <div>
                                    <label htmlFor="contactName">Name</label>
                                    <input name="contactName" type="text" id="contactName" value={this.state.name}
                                    onChange={e => this.setState({name: e.target.value})} />
                                </div>
                                <div>
                                    <label htmlFor="contactEmail">Email <span className="required">*</span></label>
                                    <input name="contactEmail" type="text" id="contactEmail" value={this.state.email} required
                                    onChange={e => this.setState({email: e.target.value})} />
                                </div>
                                <div>
                                    <label htmlFor="contactSubject">Subject</label>
                                    <input name="contactSubject" type="text" id="contactSubject" value={this.state.subject} 
                                     onChange={e => this.setState({subject: e.target.value})} />
                                </div>
                                <div>
                                    <label  htmlFor="contactMessage">Message <span className="required">*</span></label>
                                    <textarea name="contactMessage"  id="contactMessage" rows="15" cols="50" required  value={this.state.message}
                                    onChange={e => this.setState({message: e.target.value})} >
                                    </textarea>
                                </div>
                                <div>
                                    <button className="submit">Submit</button>
                                    <span id="image-loader">
                                    <img src="images/loader.gif" alt="" />
                                    </span>
                                </div>
                            </fieldset>
                        </form> 
                        <div id="message-warning"></div>
                        <div id="message-success">
                            <i className="icon-ok"></i>Your message was sent, thank you!<br />
                        </div>
                    </div>
                    <aside className="col g-5">
                        <h3>Contact Information</h3>
                        <p>
                        Phone: +91 979 119 4570 <br />
                        Email: preethiramiah@gmail.com
                        </p>
                    </aside>
                </div>
            </section>
        )
    }
}